.dcl.buy-mana-with-fiat-modal .dcl.option .info {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  margin-left: 16px;
}

.dcl.buy-mana-with-fiat-modal .dcl.option .info .cta {
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  color: var(--text);
}

.dcl.buy-mana-with-fiat-modal .dcl.option .info .ctaSubtitle {
  color: var(--text);
  margin-top: 4px;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
}

.dcl.buy-mana-with-fiat-modal .dcl.option + .dcl.option {
  margin-top: 16px;
}

.dcl.buy-mana-with-fiat-modal .message {
  margin-top: 28px;
  text-align: center;
  display: block;
  color: var(--secondary-text);
  line-height: 1.5;
  opacity: 0.9;
}

.dcl.buy-mana-with-fiat-modal .error {
  opacity: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  color: var(--primary);
  margin: 20px 0;
  text-align: center;
}

.dcl.buy-mana-with-fiat-modal .error.visible {
  transition: opacity 0.25s ease;
  opacity: 1;
  -webkit-user-select: auto;
     -moz-user-select: auto;
          user-select: auto;
}

.dcl.buy-mana-with-fiat-modal .loader-background {
  height: 100%;
  width: 100%;
  background-color: var(--background);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.5;
}
