.CatalogCard {
  height: 360px;
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  border: var(--outline);
  border-radius: 8px;
  background-color: var(--card);
  max-width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 0;
  padding: 0;
  color: var(--text);
}

.CatalogCard .dui-asset-image {
  border-radius: 8px 0px 8px 0px;
  height: 209px;
  transition: height 0.3s;
  width: 100%;
}

.CatalogCard .badge {
  margin-top: 10px;
}

.CatalogCard .textOverflow {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.CatalogCard .header .title {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-bottom: 0px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 24px;
}

.CatalogCard .creator {
  font-size: 14px;
  font-weight: 400;
  color: var(--secondary-text);
}

.CatalogCard.ui.card > .content > .header:not(.ui) {
  margin-bottom: 0px;
  flex: unset;
}

.CatalogCard .CatalogItemInformation {
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  transition: inherit;
  flex: 1;
}

