.ui.loader:before {
  border-color: var(--secondary) !important;
}

.ui.loader:after {
  border-color: var(--primary) transparent transparent !important;
}

.ui.loader.injected,
.ui.loader.formatic,
.ui.loader.wallet_connect {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 75%;
}

