.EmoteControls {
  display: flex;
  margin: 16px 0;
  padding: 0 24px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.EmoteControls .ui.button.play-control {
  width: 56px;
  height: 44px;
  padding: 0;
  min-width: unset;
  margin-right: 16px;
  background: rgba(0, 0, 0, 0.64) !important;
}

.EmoteControls .ui.button.sound-control {
  margin-left: 20px;
  min-width: 25px;
  background: none;
  padding: 0;
}

