.dui-category-filter.dcl.box {
  background-color: var(--card);
  border-radius: 10px;
  border: none;
  padding: 0px;
}

.dui-category-filter.dcl.box .box-header {
  padding: 20px 20px 10px 20px;
  color: var(--secondary-text);
  font-weight: normal;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .dui-category-filter__header {
    display: flex;
    justify-content: space-between;
    text-transform: none;
    font-size: 17px;
  }

  .dui-category-filter.dcl.box {
    background: transparent;
  }

  .dui-category-filter__name {
    color: #fff;
  }

  .dui-category-filter .dui-side-menu-item {
    font-weight: 400;
  }

  .dui-category-filter .dui-side-menu-item--active {
    border-radius: 6px;
    font-weight: 500;
  }

  .dui-category-filter .dui-side-menu-item--level-1 {
    padding-left: 30px;
  }

  .dui-category-filter .dui-side-menu-item--level-2 {
    padding-left: 36px;
  }

  .dui-category-filter .dui-side-menu-item--level-3 {
    padding-left: 52px;
  }

  .dui-category-filter .dui-side-menu-item--active::before {
    top: 18px;
    left: 10px;
    position: absolute;
    content: '';
    width: 14px;
    height: 10px;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='18' height='14' viewBox='0 0 18 14'%3E%3Cpath fill='%23FFF' fill-rule='nonzero' d='M.58 7.796a.847.847 0 0 1-.247-.557c0-.16.082-.398.246-.557l1.15-1.115a.81.81 0 0 1 1.148 0L6.138 9.05c.165.159.41.159.575 0l8.41-8.478a.81.81 0 0 1 1.149 0l1.149 1.115a.754.754 0 0 1 0 1.114L6.959 13.428a.756.756 0 0 1-.574.239.756.756 0 0 1-.575-.24L.58 7.797z'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    border: none;
    filter: invert(57%) sepia(80%) saturate(7429%) hue-rotate(335deg)
      brightness(84%) contrast(103%);
  }
}
